@import url(https://fonts.googleapis.com/css?family=Monoton);
@import url(https://fonts.googleapis.com/css?family=Lekton);
@import url(https://fonts.googleapis.com/css?family=Staatliches);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.center-typisc {
  margin: 0 auto;
}

.phrase0 {
  font-family: 'Lekton';
  font-size: 25px;
  font-weight: 700;
  text-shadow: 2px 2px 5px black;
}

.father-container {
  width: 100%;
}

.main {
  height: 100vh;
}

.title {
  font-family: 'Monoton';
  font-size: 50px;
  font-weight: 550;
  margin-top: 25vh;
  text-shadow: 2px 2px 5px black;
}

.subtitle {
  font-family: 'Lekton';
  font-size: 30px;
  font-weight: 600;
  text-shadow: 2px 2px 5px black;
}
.header-asserts {
  padding: 0 !important;
  min-width: 150px;
}

.header-content {
  background-color: #282c34;
  border-right: 1px solid rgba(0,0,0,.3);
  box-shadow: 3px 0px 30px rgba(0,0,0,.2),
  5px 0px 20px black;
  height: 100%;
  min-width: 150px;
}

.menu-list {
  margin-top: 30px;
  padding: 0;
}

.menu-list li {
  list-style-type: none;
  padding: 5px;
}

.menu-list li a {
  color: antiquewhite;
  font-family: 'Staatliches';
  text-decoration: none;
}

.menu-list-icon li {
  list-style-type: none;
}

.menu-list-icon li a {
  color: white;
  font-size: 35px;
  text-decoration: none;
}

.menu-list-icon li a:hover,
.menu-list li a:hover {
  color: rgb(145, 167, 189)
}


.logo-img {
  margin-top: 25vh;
  width: 50px;
}
.card-body {
  padding: 5px !important;
}

.card-title {
  font-size: 16px;
}

.card-text {
  font-size: 14px;
  padding: 2px;
  margin: 0;
}

.project-card {
  box-shadow: 2px 2px 10px black;
  border: none;
  cursor: pointer;
  height: 280px;
  margin: 0px 0px 5px;
  padding: 0px;
  color: black;
  width: 250px;
  transition: transform 0.4s;
}

.project-card:hover { 
  transform: scale(1.05, 1.05);
  color: black;
  text-decoration: none;

}

.project-img {
  height: 45%;
}

.project-title {
  font-weight: 700;
}

.features {
  margin-top: 0px;
}

.under-construction {
  position: relative;
  bottom: 200px;
  left: 50px;
  padding: 0 10px;
  background-color: yellow;
  border: 1px solid black;
  border-radius: 10px;
  font-weight: 900;
}
.projects {
  height: 100vh;
  padding: 20px 15px;
  margin: 0;
} 
.App {
  text-align: center;
  margin-top: 120px;
  border-radius: 10px;
  padding:80px;
  margin: 80px auto;
  width: 700px;
  background-color: white;
}

.button-position, .twitter-button-position {
  margin: 10px 5px;
}

.wrapper {
  width: 100%;
  height: 100vh;
}




.timeline {
  padding: 50px;
}

.sizer {
  height: 120px;
}

.experience-card {
  box-shadow: 2px 2px 10px black;
  cursor: pointer;
  width: 100% !important;
  height: 300px;
  margin: 10px 0px 5px;
  padding: 7px;
  color: black;
  width: 280px;
  transition: transform 0.4s;
}
body {
  background-color: rgb(221, 221, 223);
  background-size: cover;
  min-width: 600px;
}
